<template>
  <div>
    <div class="content">
      <div class="close" @click="back">
        <van-icon name="cross" />
      </div>

      <div class="pop_cont_t">
        <main class="cont_t">
          <div class="log_cont">
            <!-- 登录后 -->
            <div class="user_mess" v-if="this.$store.state.count == 0">
              <van-image class="user_pic" width="50" height="50" src round />
              <div class="user_naem">
                <span>{{user.user_name}}</span>
              </div>
            </div>
            <!-- 未登录 -->
            <div class="log">
              <h4>欢迎来来到企鹅视频</h4>

              <van-cell class="but" @click="showPopup">
                <van-button type="primary" size="small">
                  <span>登录/注册</span>
                </van-button>
              </van-cell>
            </div>
          </div>
          <div>
            <span>会员中心</span>
            <span>日期</span>
          </div>
        </main>
      </div>
    </div>
    <van-popup v-model="show" position="bottom" :style="{height:'50%'}" round>
      <!-- 登录页面 -->
      <div class="login hide" :class="[active ? 'active' : 'hide']">
        <h4>欢迎来到企鹅视频^v^ 请先登录哦~</h4>
        <!-- <van-form @submit="onSubmit"> -->
        <van-field v-model="user.username" name="用户名" label="用户名" placeholder="用户名" />
        <van-field v-model="user.password" type="password" name="密码" label="密码" placeholder="密码" />
        <van-field name="radio" label="单选框">
          <template #input>
            <van-checkbox v-model="checked">协议书</van-checkbox>
          </template>
        </van-field>
        <div class="button">
          <van-button
            round
            block
            type="info"
            native-type="submit"
            @click="onSubmit(user.username,user.password,checked)"
          >登录</van-button>
          <van-button round block type="info" native-type="submit" @click="registered()">注册</van-button>
        </div>
        <!-- </van-form> -->
      </div>
      <!-- 注册页面 -->
      <div class="res active" :class="[active ? 'active' : 'active']">
        <h4>欢迎来到企鹅视频^v^ 欢迎注册~</h4>
        <div class="registered">
          <van-field
            v-model="res.username"
            name="用户名"
            label="用户名"
            placeholder="用户名"
            @blur="check"
            :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
            v-model="res.password"
            type="password"
            name="密码"
            label="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
          />
          <van-field
            v-model="res.phone"
            type="phone"
            name="手机"
            label="手机"
            placeholder="手机"
            :rules="[{ required: true, message: '请填写手机' }]"
          />
          <!-- <label for="email">邮箱:</label><input id="email" name="email" type="email" />  -->
          <div style="margin: 16px;">
            <van-button round block type="info" @click="submit" native-type="submit">提交</van-button>
          </div>
        </div>
        <div class="return">
          <span>已经有账号?</span>
          <span @click="registered()" style="color:#409EFF">返回登录</span>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
//引用axios
import axios from "axios";
export default {
  data() {
    return {
      checked: true,
      show: true,
      // 显示登录注册的添加
      active: true,
      // 用户信息
      user: {
        username: "",
        password: "",
        userid: ""
      },
      // 用户注册信息
      res: {
        username: "",
        password: "",
        phone: ""
      }
    };
  },
  methods: {
    back() {
      this.$router.push("/");
    },
    showPopup() {
      console.log(11111);
      this.show = true;
    },
    // 用户登录
    onSubmit(name, password, checked) {
      this.user.username = name;
      this.user.password = password;
      console.log(this.user.username);
      console.log(this.user.password);
      console.log(checked);
      if (this.user.username == "" || this.user.password == "") {
        this.$toast.allowMultiple();
        this.$toast("你还没输入账号或者密码呢");
      } else if (this.user.username.length > 3) {
        console.log("太长了");
        this.$toast.allowMultiple();
        this.$toast("太长了");
      } else if (checked == false) {
        this.$toast.allowMultiple();
        this.$toast("先要同意协议才能登陆嗷");
      } else {
        console.log("成功登陆");
      }
    },
    // 显示用户登录注册
    registered() {
      if (this.active) {
        this.active = false;
      } else {
        this.active = true;
      }
    },
    // 用户注册方法
    submit() {
      // if (!/^(\w){4,20}$/.test(this.res.username)) {
      //     this.$toast.allowMultiple();
      //     this.$toast("用户名不正确");
      // } else if (!/^(\w){4,20}$/.test(this.res.password)) {
      //     this.$toast.allowMultiple();
      //     this.$toast("密码不符合规则");
      // } else if (!/^1[3|4|5|8][0-9]\d{4,8}$/.test(this.res.phone)) {
      //     this.$toast.allowMultiple();
      //     this.$toast("手机号码不正确");
      // }
      // else {
      //   }
      // let that = this;
      // 查询用户名是否被注册
      let name = this.res.username;
      console.log(name);
      axios
      // https://panyaoan.github.io/GitTest/dist/js/app.47e14adb.js
      //192.168.1.167:8081/login
        .get("//panyaoan.github.io/GitTest/dist/js/app.47e14adb.js:8081/login", {
          // headers: {
          //   "content-type": "application/json",
          //   // 自动将http的不安全请求升级为https不然在github线上会报
          //   "Content-Security-Policy": "upgrade-insecure-requests"
          // },
          params: {
            name: name
          }
        })
        .then(response => {
          console.log(response);
          let username = response.data;
          // console.log(response.data);

          // console.log(username.length);
          if (username.length >= 1) {
            this.$toast.allowMultiple();
            this.$toast("用户名已被注册,请更换一个");
          } else {
            console.log(this.res.username);
            console.log(this.res.password);
            console.log(this.res.phone);
            let useData = this.res;
            console.log(useData);
            // axios请求 后台请求
            axios
              .post("//panyaoan.github.io/GitTest/dist/js/app.47e14adb.js:8081/login", useData)
              .then(res => {
                console.log(res);
              })
              .catch(res => {
                console.log(res);
              });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    check() {
      console.log(1111);
    }
  },
  mounted() {}
};
</script>
<style scoped>
.active {
  display: block;
}
.hide {
  display: none;
}
.content {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
}
.pop {
  display: flex;
}
.pop_cont_t {
  display: flex;
  margin: auto;
  width: 100%;
  height: 100%;
  background-color: #dfdfdf;
}
/* 关闭遮罩层按钮样式*/
.close {
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 25px;
}
.cont_t {
  width: 100%;
  /* height: 100%; */
  padding: 15px;
  text-align: left;
}
/* 登录前的样式 */
.log_cont {
  /* height: 100%; */
  width: 100%;
  background: #ffffff;
  display: flex;
}

.but {
  padding: 0px;
}
/* 登录后的样式 */
.user_mess {
  /* display: flex; */
  padding: 15px 0;
  background-color: #ffffff;
}
.user_naem {
  padding: 5px 0;
}
/* 按钮样式 */
.button {
  display: flex;
}
.button button {
  margin: 15px;
}
</style>